.user-auth {
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

form {
    max-width:400px;

}

form h1 {
    text-align: center;
    font-weight: 300;
    font-size: 50px;
    margin-bottom: 20px;
}



.input-form {
    position: relative;
    width: 100%;
    padding:15px;
    margin-bottom: 4px;
}

.input-box {
    padding:8px 10px 8px 10px;
}

.fa-solid {
    cursor:pointer;
}

.fa-regular {
    cursor:pointer;
}



button {
    padding:8px 10px 8px 10px;
    background-color: rgb(94, 58, 4);
    color: white;
    border-radius: 3px;
    border-style: none;
    cursor:pointer;
}

.link {
    margin-left:5px;
    color:rgb(94, 58, 4);
}
    .link:hover {
        color: black;
    }
    .link:active {
        color: black;
    }
